.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.page {
  position: relative; }
  .page .page-description {
    max-width: 700px;
    margin: 0 auto;
    background: #392270;
    border: 3px solid #5736A7;
    font-family: 'Open Sans', sans-serif;
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 25px; }
    .page .page-description a {
      color: #ffff66;
      -webkit-transition: 0.3s all;
      transition: 0.3s all;
      text-decoration: none; }
      .page .page-description a:hover, .page .page-description a:focus {
        text-decoration: underline; }
    @media screen and (max-width: 900px) {
      .page .page-description {
        margin-bottom: 0px;
        border-radius: 0px;
        border-left: 0px;
        border-right: 0px; }
        .page .page-description.no-top-border {
          border-top: 0px; } }
  .page .page-image-wrapper {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 25px;
    cursor: -webkit-zoom-in;
    cursor: zoom-in;
    -webkit-transition: 0.3s all;
    transition: 0.3s all; }
    .page .page-image-wrapper img {
      max-width: 725px;
      width: 100%;
      border: 3px solid #5736A7;
      border-radius: 10px; }
    .page .page-image-wrapper.no-border img {
      border: 0px; }
    .page .page-image-wrapper:focus {
      outline: none; }
      .page .page-image-wrapper:focus img {
        border-color: #ffff66; }
    @media screen and (max-width: 900px) {
      .page .page-image-wrapper {
        margin-bottom: 0px; }
        .page .page-image-wrapper img {
          border-radius: 0px;
          border-top: 0px; } }
  .page .side-by-side-image {
    display: flex;
    align-items: stretch;
    justify-content: center; }
    .page .side-by-side-image .page-image-wrapper img {
      border-top-right-radius: 0px;
      border-bottom-right-radius: 0px; }
    .page .side-by-side-image .page-image-wrapper + .page-image-wrapper {
      margin-left: 2px; }
      .page .side-by-side-image .page-image-wrapper + .page-image-wrapper img {
